.page__services {

    padding: 13px 0 60px 0;

    @include breakpoint(md) {
        padding: 40px 0 60px 0;
    }

    @include breakpoint(lg) {
        padding: 40px 0 115px 0;
    }

}
.container {
}
.page__services-title {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #2B2A29;

    @include breakpoint(md) {
        font-size: 36px;
    }

}
.page__services-text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #000000;
    margin-top: 33px;
    margin-bottom: 66px;

    
    @include breakpoint(lg) {
        max-width: 726px;
    }
}
.page__services-items {
    display: flex;
    flex-wrap: wrap;
    margin-left: -20px;


    @include breakpoint(lg) {
        margin-left: -30px;
    }

}
.page__services-item {
    display: flex;
    flex-direction: column;
    width: calc( 100% / 2 - 20px);
    margin-left: 20px;
    margin-bottom: 20px;


    @include breakpoint(lg) {
        width: calc( 100% / 3 - 30px);
        margin-left: 30px;
        margin-bottom: 30px;
    }

}
.page__services-item-img {
    width: 100%;


    @include breakpoint(md) {
        min-height: 200px;
        max-height: 200px;
    }

}
.page__services-item-title {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #2B2A29;
    position: relative;
    margin-top: 15px;
    padding-left: 20px;

    &::after {
        position: absolute;
        content: '';
        height: 100%;
        width: 1px;
        background: #00A0E3;
        left: 0;
        top: 0;
    }


    @include breakpoint(lg) {
        font-size: 16px;
        margin-top: 18px;
    }


}


.seo__section {
    margin-top: 50px;
    background-color: #fff;
    padding: 25px 8px;

    p, li {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #535352;
        margin-bottom: 20px;
    }

    h2 {
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 140%;
        color: #2B2A29;
        margin-bottom: 30px;

        @include breakpoint(lg) {
            font-size: 36px;
            margin-bottom: 50px;
        }

    }

    @include breakpoint(md) {
        padding: 25px 18px;
    }

    @include breakpoint(lg) {
        margin-top: 90px;
        padding: 40px 100px;
    }


}