.page__item {
    padding: 0 0 75px;

    @include breakpoint(lg) {
        padding: 32px 0 75px;
    }
}

.page__item .container {

    @include breakpointMax(lg) {
        padding: 0;
    }
}

.page__item section {
    padding: 25px 10px 40px;
    border-top: 1px solid #EAEAEA;

    @include breakpoint(md) {
        padding: 25px 25px 40px;
    }

    @include breakpoint(xl) {
        padding: 40px 38px;
    }
}

.item-head {
    margin-bottom: 35px;

    @include breakpoint(lg) {
        margin-bottom: 40px;
    }
}

.section-title {
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #2B2A29;

    @include breakpoint(lg) {
        font-size: 24px;
    }
}

.item-head .section-title {
    margin-bottom: 0;
}

.page-item-wrapper {

    @include breakpoint(lg) {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
}

.item__container {

    @include breakpoint(lg) {
        width: calc(75% - 15px);
    }

    @include breakpoint(xl) {
        width: calc(100% - 15px);
        max-width: 892px;
    }
}

.item__container-wrap {
    padding: 30px 0 36px;
    background: #fff;
}

.item__wrapper {
    padding: 0 10px 25px;

    @include breakpoint(md) {
        padding: 0 25px 25px;
    }

    @include breakpoint(lg) {
        display: flex;
        justify-content: space-between;
    }

    @include breakpoint(xl) {
        padding: 0 38px 45px;
    }
}

.item__swiper {
    display: flex;
    justify-content: space-between;

    @include breakpointMax(lg) {
        height: calc((((((100vw - 20px) / 6) * 5) - 20px) * 1.53));
        max-width: 400px;
        max-height: 475px;
    }

    @include breakpoint(lg) {
        justify-content: unset;
        width: 50%;
        max-width: 392px;
        max-height: 390px;
    }

    @include breakpoint(xl) {
        max-height: 464px;
    }
}

.item-swiper {

    @include breakpointMax(lg) {
        margin: 0;
        width: calc(((100% / 6) * 5) - 20px);
    }
}

.item-swiper .item__block {
    height: 100%;
}

.item-swiper-pagination {

    @include breakpointMax(lg) {
        margin: 0;
        width: 66px;
    }

    @include breakpoint(lg) {
        margin-left: 22px;
        flex-shrink: 0;
    }
}

.item-swiper-pagination .swiper-slide {
    height: auto;
}

.item-swiper-pagination .item__block {
    width: 62px;
    height: 62px;
    border: 2px solid #EAEAEA;
    cursor: pointer;

    @include breakpoint(lg) {
        width: 45px;
        height: 45px;
    }

    @include breakpoint(xl) {
        width: 65px;
        height: 65px;
    }
}

.item-swiper-pagination .swiper-slide-thumb-active .item__block {
    border-color: $blue;
}

.item-swiper-pagination .item__block img {
    object-fit: cover;
}

.item__description {

    @include breakpoint(lg) {
        width: calc(50% - 30px);
        max-width: 338px;
    }

    @include breakpoint(xl) {
        margin-right: 34px;
    }
}

.item__title {
    margin-top: 20px;
    margin-bottom: 18px;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: $black;

    @include textLimit(2);

    @include breakpoint(lg) {
        margin-top: 0;
        margin-bottom: 28px;
        font-size: 36px;
    }
}

.item__desc-list {

    @include breakpointMax(lg) {
        max-width: 338px;
    }
}

.item__desc-list li {
    margin-bottom: 18px;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: #535352;
}

.item__action {
    margin-top: 20px;

    @include breakpointMax(lg) {
        display: flex;
    }

    @include breakpoint(md) {
        max-width: 500px;
    }

    @include breakpoint(lg) {
        margin-top: 50px;
        max-width: 205px;
    }
}

.item__action .button-style {
    display: flex;
    align-items: center;
    justify-content: center;

    @include breakpointMax(lg) {
        padding: 13px 20px;
        width: 100%;
    }

    @include breakpoint(lg) {
        width: 100%;
        height: 46px;
    }
}

.item__action .button-style--transparent {
    margin-top: 12px;
    font-size: 14px;
    line-height: 140%;
    color: $black;
    border: 1px solid #2B2A29;

    @include breakpointMax(lg) {
        margin-top: 0;
        margin-left: 10px;
    }
}

.item__char-table-row {
    padding: 12px 11px;
    display: flex;
    justify-content: space-between;
}

.item__char-table-row:nth-child(odd) {
    background: #EAEAEA;
}

.char-label {
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: $black;
}

.char-weight {
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: #535352;
}

.item-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.item__slider-arrows {
    display: flex;
    align-items: center;
}

.item__slider-arrows .button {
    cursor: pointer;
}

.item__slider-arrows .button ~ .button {
    margin-left: 25px;
}

.item__slider-arrows .button.swiper-button-disabled {
    color: #CDCCCC;
}

.slider-desc p {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #535352;
}

.item__slider-label {
    margin-top: 10px;
    font-size: 12px;
    line-height: 15px;
    color: $black;

    @include textLimit(2);

    @include breakpoint(s) {
        text-align: center;
    }

    @include breakpoint(lg) {
        text-align: unset;
    }
}

.item__slider-type {
    margin-top: 10px;
    font-size: 12px;
    line-height: 15px;
    color: #A4A4A3;

    @include textLimit(2);

    @include breakpoint(s) {
        text-align: center;
    }

    @include breakpoint(lg) {
        text-align: unset;
    }
}

.item__slider--1 .item__slider-img,
.item__slider--2 .item__slider-img {
    height: 205px;
    overflow: hidden;
}

.item__slider--3 .swiper-wrapper {
    flex-direction: row;
    max-height: 230px;
}

.item__slider--3 .swiper-slide {
    margin-top: 0!important;
    margin-bottom: 15px;;
}

.item__slider--3 .item__slider-img {
    height: 43px;
    overflow: hidden;

    @include breakpoint(md) {
        height: 55px;
    }

    @include breakpoint(lg) {
        height: 58px;
    }
}

.item__slider--4 .item__slider-img {
    height: 111px;
    overflow: hidden;
}

.item__slider--5 .swiper-wrapper {
    flex-direction: row;
    max-height: 350px;
}

.item__slider--5 .swiper-slide {
    margin-top: 0!important;
    margin-bottom: 25px;;
}

.item__slider--5 .item__slider-img {
    height: 75px;
    overflow: hidden;

    @include breakpoint(md) {
        height: 125px;
    }

    @include breakpoint(lg) {
        height: 111px;
    }
}

.item__return-btn {
    margin-left: 10px;
    color: #000;
    border: 1px solid #2291C0;

    @include breakpoint(md) {
        margin-left: 25px;
    }

    @include breakpoint(lg) {
        margin-left: 25px;
    }

    @include breakpoint(xl) {
        margin-left: 38px;
    }
}

.another-section {
    margin-top: 30px;
    padding: 0 10px;

    @include breakpoint(lg) {
        margin-top: 75px;
        padding: 0;
    }
}

.another-section .item-head {
    margin-bottom: 30px;

    @include breakpoint(lg) {
        margin-bottom: 50px;
    }
}

.another-section .section-title {

    @include breakpoint(lg) {
        font-size: 36px;
    }
}

.item__slider--6 .swiper-slide {
    padding: 17px 12px 13px;
    background: #fff;
    box-sizing: border-box;

    @include breakpoint(lg) {
        padding: 30px 38px 17px;
    }
}

.item__slider--6 .item__slider-img {
    height: 214px;
    overflow: hidden;

    @include breakpoint(xl) {
        height: 308px;
    }
}

.item__slider--6 .item__slider-label {
    font-weight: 500;
    font-size: 12px;
    line-height: 130%;

    @include breakpoint(lg) {
        font-size: 14px;
    }
}