.page__news {
    padding: 30px 0 50px 0;
    background: #D5D5D5;

    @include breakpoint(lg) {
        padding: 45px 0 117px 0;
    }
}

.page__news-items {
    display: flex;
    flex-wrap: wrap;


    @include breakpoint(md) {
        margin-left: -20px;
    }

    @include breakpoint(lg) {
        margin-left: -30px;
    }

}

.page__news-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    width: 100%;

    @include breakpoint(md) {
        width: calc( 100% / 2 - 20px);
        margin-left: 20px;
    }

    @include breakpoint(lg) {
        width: calc( 100% / 3 - 30px);
        margin-left: 30px;
    }
}

.page__news-item-img {
    border: 1px solid #E1E1E1;
    width: 100%;
    background-color: #FFFFFF;
    img {
     
    }

    @include breakpoint(md) {
        min-height: 220px;
        max-height: 220px;
    }

}

.page__news-item-date {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #A4A4A3;
    margin: 20px 0;
}

.page__news-item-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    color: #2B2A29;
}

.page__pagination {
    display: flex;
    flex-direction: column;
    margin-top: 75px;

    @include breakpoint(lg) {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin-top: 100px;
    }

}

.page__pagination-wrapp {
    display: flex;
}

.show-more__btn {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    text-transform: uppercase;
    color: #FFFFFF;
    background: #00A0E3;
    padding: 13px 30px;
    display: flex;

    i {
        margin-left: 10px;
    }

}

.show-more__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #7C7B7B;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

    @include breakpoint(lg) {
        margin-left: 35px;
    }

}

.pagination {
    margin-top: 12px;

    @include breakpoint(lg) {
        margin-top: 0;
    }

}

.nav-links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background: #FFFFFF;
    padding: 11px;
}

.page-numbers {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 13px;
    line-height: 140%;
    color: #CDCCCC;
    padding: 0 7px;

    @include breakpoint(lg) {
        padding: 0 10px;
    }

}

.page-numbers.current {
    color: #00A0E3;
}

.page-numbers.dots {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 13px;
    line-height: 140%;
    color: #CDCCCC;
}

.prev.page-numbers {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 140%;
    text-transform: uppercase;
    color: #CDCCCC;
    height: 35px;
    display: flex;
    align-items: center;
    border-right:  1px solid #EAEAEA;
    padding: 0 17px;
    margin-right: auto;
}

.next.page-numbers {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 140%;
    text-transform: uppercase;
    color: #CDCCCC;
    height: 35px;
    display: flex;
    align-items: center;
    border-left:  1px solid #EAEAEA;
    padding: 0 17px;
    margin-left: auto;
}