.page__news-detail {
    padding: 20px 0 90px 0;

    @include breakpoint(lg) {
        padding: 70px 0 117px 0;
    }

}

.news-detail {
    display: flex;
    flex-wrap: wrap;

    @include breakpoint(lg) {
        flex-direction: row;
    }


}

.news-detail__img {
    width: 100%;
    background: #fff;
    border: 1px solid #E1E1E1;
    max-height: 500px;

    @include breakpoint(lg) {
        width: 30%;
        max-height: 380px;


        img {
            object-fit: cover;
        }

    }

}

.news-detail__content {
    margin-top: 50px;

    @include breakpoint(lg) {
        width: calc( 70% - 130px);
        padding-left: 130px;
        margin-top: 0;
    }

}

.news-detail__date {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #A4A4A3;
}

.news-detail__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #2B2A29;
    margin: 40px 0 60px 0;

    @include breakpoint(lg) {
        max-width: 460px;
    }

}

.news-detail__text {

    p, li {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #000000;
        margin-bottom: 15px;
    }

}

.news-detail__btn {
    display: flex;
    align-items: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    text-transform: uppercase;
    color: #2B2A29;
    padding: 13px 30px;
    border: 1px solid #2291C0;
    width: fit-content;
    margin-top: 30px;

    i {
        margin-right: 10px;
    }


    @include breakpoint(md) {
        margin-top:60px;
    }

}