.page__contacts {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.page__contacts-top {
    padding: 30px 0 70px 0;
    background: #F6F6F6;
    width: 100%;

    @include breakpoint(lg) {
        padding: 40px 0 45px 0;
    }

}

.page__contacts-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #2B2A29;
    margin-bottom: 45px;

    @include breakpoint(lg) {
        font-style: normal;
        font-weight: 500;
        font-size: 36px;
        line-height: 140%;
        margin-bottom: 65px;
    }

}

.page__contacts-items {
    display: flex;
    flex-wrap: wrap;

    @include breakpoint(md) {
        margin-left: -20px;
    }


    @include breakpoint(lg) {
        margin-left: -30px;
    }

}

.page__contacts-item {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 65px;

    &:last-child {
        margin-bottom: 0;
    }

    @include breakpoint(md) {
        width: calc(100% / 2 - 20px);
        margin-left: 20px;
    }

    @include breakpoint(lg) {
        width: calc(100% / 3 - 30px);
        margin-left: 30px;
        margin-bottom: 0;
    }
}


.page__contacts-item-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #4B4B4B;
    margin-bottom: 20px;

    @include breakpoint(lg) {
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 140%;
        color: #4B4B4B;
        margin-bottom: 30px;
    }

}

.page__contacts-item-link {
    display: flex;
    flex-direction: row;


    img {
        width: 14px;
        height: 20px;
        margin-right: 12px;
    }

    span {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #333333;
    }

}

.page__contacts-item-cords {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 1px solid #c5c5c5;
    border-radius: 5px;
    max-width: 355px;
    padding: 20px 30px;

    img {
        width: 20px;
        height: 28px;
        margin-right: 55px;
    }

    span {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #4B4B4B;
    }

}

.page__contacts-bottom {
    padding: 30px 0 70px 0;
    background: #fff;
    width: 100%;

    @include breakpoint(lg) {
        padding: 65px 0;
    }

}

.page__contacts-bottom-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}


.page__contacts-form {
    width: 100%;

    form {

        display: flex;
        flex-wrap: wrap;

        .form-row {
            width: 100%;
            display: flex;
            flex-direction: column;
            margin-bottom: 47px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .form-title {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 150%;
            color: #4B4B4B;
            margin-bottom: 18px;

            @include breakpoint(lg) {
                margin-bottom: 30px;
            }

        }

        .form-label {

            margin-bottom: 20px;

            input {
                width: 100%;
                padding: 11px 5px;
                border-bottom: 1px solid #e9e9e9;
                box-sizing: border-box;
            }

            textarea {
                width: 100%;
                padding: 11px 15px;
                border: 1.5px solid #ECEEF1;
                box-sizing: border-box;
                min-height: 137px;
            }

            button {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 13px;
                width: 100%;
                background: #00A0E3;

                span {
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 140%;
                    text-transform: uppercase;
                    color: #FFFFFF;
                    margin-right: 10px;
                }

                img {
                    width: 14px;
                    height: 20px;
                }

            }


        }

        .form-group {

            label {
                display: flex;
                align-items: center;
            }

        }





        .form-group input {
            padding: 0;
            height: initial;
            width: initial;
            margin-bottom: 0;
            display: none;
            cursor: pointer;
        }

        .form-group label {
            position: relative;
            cursor: pointer;


            span {
                font-family: 'Montserrat';
                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 120%;
                color: #757575;

                a {
                    color: #00A0E3;
                }

            }

        }

        .form-group label:before {
            content: '';
            -webkit-appearance: none;
            background-color: transparent;
            border: 2px solid #E8E8E8;
            box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
            padding: 10px;
            display: inline-block;
            position: relative;
            vertical-align: middle;
            cursor: pointer;
            margin-right: 16px;
            max-height: 10px;
        }

        .form-group input:checked+label:after {
            content: '';
            display: block;
            position: absolute;
            top: 4px;
            left: 10px;
            width: 6px;
            height: 14px;
            border: solid #00A0E3;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
        }

        @include breakpoint(lg) {
            margin-left: -30px;
        }

    }

    @include breakpoint(lg) {
        width: calc(100% / 2 - 30px);
        margin-left: 30px;
    }


}


.page__contacts-map {
    margin-top: 67px;
    width: 100%;

    #map {
        width: 100%;
        height: 260px;
        border: 1px solid #00A0E3;

        @include breakpoint(lg) {
            height: 425px;
        }
    }


    @include breakpoint(lg) {
        width: calc(100% / 2 - 30px);
        margin-left: 30px;
    }

}

.page__contacts-map-text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #4B4B4B;
    margin-top: 20px;
}