.page__download {
    padding: 13px 0 68px 0;


    @include breakpoint(md) {
        padding: 30px 0 60px 0;
    }


    @include breakpoint(lg) {
        padding: 40px 0 90px 0;
    }


}

.container {}

.page__download-title {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #2B2A29;
    margin-bottom: 50px;

    @include breakpoint(lg) {
        font-size: 36px;
    }  

}

.download__items {
    display: flex;
    flex-wrap: wrap;
}

.download__item {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-bottom: 20px;


    &:last-child{
        margin-bottom: 0;
    }

}

.download__item-icon {
    width: 27px;
    height: 33px;
    margin-right: 12px;
}

.download__item-content {
    display: flex;
    flex-direction: column;
}

.download__item-title {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #2B2A29;
}

.download__item-format {

    display: flex;
    align-items: center;

    span {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: #00A0E3;  
    }

    div {
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 140%;
        margin-left: 5px;
        color: #535352;
    }
}