.page__category {
    padding: 15px 0 40px;

    @include breakpoint(lg) {
        padding: 30px 0 86px;
    }
}

.cat {

    @include breakpoint(lg) {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
}

.cat__filter-mobile-btn {
    margin: 0 auto;
    background: $black;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 355px;
    text-align: center;
    margin-bottom: 20px;

    @include breakpoint(lg) {
        display: none;
    }
}

.cat__filter-mobile-btn::after {

    @include breakpointMax(lg) {
        content: '';
        margin-top: 5px;
        margin-left: 24px;
        background: url('data:image/svg+xml;base64,') no-repeat;
        display: inline-block;
        width: 20px;
        height: 20px;
    }
}

.cat__aside {
    background: #fff;

    @include breakpointMax(lg) {
        padding: 16px 13px;
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        max-width: 304px;
        z-index: 9;
        overflow-y: scroll;
        transition: all 0.4s ease;
    }

    @include breakpoint(lg) {
        width: calc(25% - 15px);
    }

    @include breakpoint(xl) {
        width: calc(100% - 15px);
        max-width: 278px;
    }
}

.cat__aside.opened {
    left: 0;
}

.cat__aside-wrap {

    @include breakpointMax(lg) {
        border: 1px solid #EAEAEA;
    }
}

.cat__links-item {
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: $black;
    border-bottom: 1px solid #EAEAEA;
}

.cat__links-item a {
    display: block;
    width: 100%;
}

.cat__links-item.active {
    color: $blue;
}

.cat__links-item.active::after {
    content: '';
    background: url('data:image/svg+xml;base64,') no-repeat;
    display: inline-block;
    width: 16px;
    height: 16px;
}

.cat__filters-item {
    padding: 15px;
    border-bottom: 1px solid #EAEAEA;
}

.cat__filters-item p {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.cat__filters-item p::after {
    content: '';
    background: url('data:image/svg+xml;base64,') no-repeat;
    display: inline-block;
    width: 14px;
    height: 10px;
    transition: transform 0.5s ease;
}

.cat__filters-item.active p::after {
    transform: rotate(180deg);
}

.cat__filters-item p {
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: $black;
}

.cat__filter-inner {
    display: none;
}

.cat__filter-item {
    margin-top: 15px;
}

.cat__filters-btn {
    padding: 20px 0;
}

.cat__filters-btn .button-style {
    margin: 0 auto;
    display: block;
    max-width: 225px;
    line-height: 20px;
    text-align: center;
}

.cat__filters-btn .button-style ~ .button-style {
    margin-top: 18px;
}

.cat__inner {

    @include breakpoint(lg) {
        width: calc(75% - 15px);
    }

    @include breakpoint(xl) {
        width: calc(100% - 15px);
        max-width: 892px;
    }
}

.cat__top {
    display: flex;
    justify-content: space-between;
    // margin-bottom: 20px;
    @include breakpointMax(lg) {
        margin-top: 15px;
        margin-left: -10px;
        flex-wrap: wrap;
        justify-content: unset;
        // margin-bottom: 30px;
    }

    @include breakpoint(md) {
        // margin-bottom: 30px;
    }

    @include breakpoint(lg) {
        justify-content: unset;
        flex-wrap: wrap;
        margin-left: -30px;
    }

}

.cat__top-item {
    padding: 18px;
    background: #FFFFFF;
    width: calc(50% - 10px);
    border: 2px solid #E1E1E1;
    cursor: pointer;

    @include breakpoint(md) {
        width: calc(25% - 10px);
    }

    @include breakpointMax(lg) {
        margin-bottom: 10px;
        margin-left: 10px;
        padding: 10px;
        display: flex;
        max-width: 240px;
        height: 64px;
        border: 1px solid #E1E1E1;
    }

    @include breakpoint(lg) {
        width: calc( 100% / 4 - 30px);
        margin-left: 30px;
        margin-bottom: 30px;
    }

    // @include breakpoint(xl) {
    //     width: calc(25% - (90px / 4));
    // }
}

.cat__top-item.active {
    border: 1px solid #00A0E3;

    @include breakpoint(lg) {
        border: 2px solid #00A0E3;
    }
}

.cat__top-item-img {
    flex-shrink: 0;

    @include breakpoint(lg) {
        min-height: 123px;
        max-height: 123px;
    }

}

.cat__top-item p {
    margin-top: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: $black;

    @include breakpointMax(lg) {
        margin-left: 7px;
        font-size: 12px;
        line-height: 15px;
    }
}

.cat__main {
 
    margin-left: -30px;
    display: flex;
    flex-wrap: wrap;

    @include breakpointMax(lg) {
        margin-left: -8px;
    }
}

.cat__item {
    margin-left: 30px;
    margin-bottom: 30px;
    padding: 30px 38px 17px;
    background: #fff;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: calc(50% - 8px);
    cursor: pointer;

    @include breakpoint(md) {
        width: calc((100% / 3) - 8px);
    }

    @include breakpointMax(lg) {
        margin-left: 8px;
        margin-bottom: 10px;
        padding: 17px 12px 13px;
    }

    @include breakpoint(lg) {
        width: calc((100% / 3) - 30px);
    }
}

.cat__item-img {
    margin-bottom: 13px;
    height: 100%;
}

.cat__item-title {
    margin-top: auto;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: $black;

    @include breakpointMax(lg) {
        font-size: 12px;
    }
}

.cat__inner .page__pagination {
    margin-top: 12px;

    @include breakpoint(lg) {
        margin-top: 0;
    }
}

.elements {
    margin-top: 50px;
    background: #fff;

    @include breakpoint(lg) {
        margin-top: 70px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
}

.elements__img {
    position: relative;
    width: 100%;
    max-width: 378px;
    // width: calc(((100% / 12) * 4) - 22px);

    @include breakpoint(lg) {
        flex-shrink: 0;
    }
}

.element-steps {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}

.element-item {
    position: relative;
    background-color: $blue;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    cursor: pointer;
    transition: .3s;
    border-radius: 50%;
}

.element-item.open {
    background-color: $black;
}

.element-item::before {
    content: '';
    position: absolute;
    width: 36px;
    height: 36px;
    border: 3px solid rgba(255, 255, 255, 0.5);
    border-radius: 50%;
}

.element-item.open::before {
    border-color: $blue;
}

.element-item::after {
    content: '+';
    position: absolute;
    font-size: 28px;
    line-height: 21px;
    color: #fff;
    transform-origin: center center;
    transition: transform .3s;
}

.element-item.open::after {
    transform: rotate(225deg);
}

.element-item:nth-child(1) {
    top: 7%;
    left: 21%;
}

.element-item:nth-child(2) {
    top: 28%;
    left: 9%;
}

.element-item:nth-child(3) {
    top: 26%;
    left: 62%;
}

.element-item:nth-child(4) {
    top: 32%;
    left: 45%;
}

.element-item:nth-child(5) {
    top: 28%;
    left: 63%;
}

.element-item:nth-child(6) {
    top: 28%;
    left: 10%;
}

.element-item:nth-child(7) {
    top: 41%;
    left: 70%;
}

.element-item span {
    padding: 10px;
    background: rgba(43, 42, 41, 0.9);
    position: absolute;
    bottom: calc(100% + 20px);
    width: max-content;
    max-width: 200px;
    max-height: 200px;
    font-size: 14px;
    line-height: 20px;
    color: #EAEAEA;
    box-shadow: 0 0 20px rgba(0,0,0,0.2);
    border-radius: 2px;
    border: 1px solid #fff;
    cursor: default;
    opacity: 0;
    visibility: hidden;
    transition: opacity .6s, visibility .6s;
    z-index: 100;
    overflow-y: scroll;
    user-select: none;
}

.element-item span b {
    color: #EAEAEA;
}

.element-item.open span {
    opacity: 1;
    visibility: visible;
}

.elements__desc {
    margin: 0 auto;
    padding: 32px 16px 36px;

    @include breakpoint(lg) {
        padding: 40px 0;
        flex-shrink: 0;
        width: calc(((100% / 12) * 7) - 70px);
    }
}

.elements__desc-info {
    padding: 7px 20px;
    display: flex;
    align-items: center;
    max-width: 440px;
    border: 1px solid #EAEAEA;

    @include breakpoint(lg) {
        align-items: unset;
    }
}

.elements__desc-info::before {
    content: '';
    background: url('data:image/svg+xml;base64,') no-repeat;
    display: inline-block;
    flex-shrink: 0;
    width: 30px;
    height: 21px;
}

.elements__desc-info span {
    margin-left: 23px;
    font-size: 14px;
}

.elements__desc-title {
    margin-top: 32px;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;

    @include breakpoint(lg) {
        margin-top: 20px;
        font-size: 36px;
    }
}

.elements__desc-text {
    margin-top: 26px;

    @include breakpoint(lg) {
        margin-top: 48px;
    }
}