.page__services-detail {
  padding: 13px 0 60px 0;

  @include breakpoint(md) {
    padding: 40px 0 60px 0;
  }

  @include breakpoint(lg) {
    padding: 40px 0 115px 0;
  }
}
.container {
}
.services__detail {
  p {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
.services__detail-title {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  color: #2b2a29;
  margin-bottom: 32px;

  @include breakpoint(lg) {
    font-size: 36px;
    margin-bottom: 42px;
  }
}
.services__detail-text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #000000;
  padding-bottom: 30px;
}

.services__detail-title-sm {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: #2b2a29;
  margin-bottom: 25px;

  @include breakpoint(lg) {
    font-size: 32px;
    margin-bottom: 30px;
  }
}

.services__detail-offer {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;

  @include breakpoint(md) {
    flex-direction: row;
  }
}

.services__detail-offer-left {
  margin-bottom: 20px;

  ul {
    list-style: disc;
    padding-left: 20px;
    margin-bottom: 20px;
  }

  li {
    list-style: disc;
  }

  p {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #000000;
    margin-bottom: 20px;
  }

  @include breakpoint(md) {
    width: 50%;
  }
}
.services__detail-offer-right {
  @include breakpoint(md) {
    width: 50%;
    display: flex;
  }


  img {
    margin-bottom: auto;
  }

}

.services__detail-promo-wrapper {
  display: flex;
  flex-direction: column;

  @include breakpoint(md) {
    flex-direction: row;
  }
}

.services__detail-promo {
  background-color: #fff;
  padding: 23px 8px;
  margin-bottom: 30px;

  @include breakpoint(md) {
    padding: 30px;
  }

  @include breakpoint(lg) {
    padding: 30px 70px 65px 70px;
  }
}
.services__detail-promo-title {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  color: #2b2a29;
  margin-bottom: 20px;

  @include breakpoint(lg) {
    font-size: 36px;
    margin-bottom: 33px;
  }
}
.services__detail-promo-img {
  margin-bottom: 30px;
  display: flex;

  @include breakpoint(md) {
    min-width: 411px;
    min-height: 264px;
    margin-right: 30px;
  }

  img{
    margin-bottom: auto;
  }

}
.services__detail-promo-text {
}
