.page__calc {
    margin-top: 34px;
    padding: 25px 0 30px;
    background: #fff;

    @include breakpoint(lg) {
        margin-top: 0;
        padding: 48px 0 80px;
        background: none;
    }
}

.calc {

    @include breakpoint(lg) {
        padding: 38px 38px 50px;
        background: #fff;
    }
}

.calc-form__title  {
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: $black;

    @include breakpoint(lg) {
        margin-bottom: 50px;
        font-size: 36px;
    }
}

.calc-form__wrap {

    @include breakpoint(lg) {
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
    }
}

.calc-form__left {

    @include breakpoint(lg) {
        width: calc(((100% / 12) * 9));
    }

    @include breakpoint(xl) {
        width: calc(((100% / 12) * 8) - 30px);
    }
}

.calc-form-row {
    margin-top: 45px;

    @include breakpoint(lg) {
        margin-top: 0;
        display: flex;
        width: 100%;
        max-width: 692px;
    }
}

.calc-form-row ~ .calc-form-row {

    @include breakpoint(lg) {
        margin-top: 50px;
    }
}

.calc-form__label {
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: $black;

    @include breakpoint(lg) {
        margin-bottom: 0;
        width: 100%;
        max-width: 240px;
    }
}

.calc-form__slider {
    padding: 0 15px;

    @include breakpoint(lg) {
        margin-top: 12px;
        margin-left: 90px;
        padding: 0;
        width: 100%;
        max-width: 368px;
    }
}

.calc-form__slider .slider-results input {
    width: 24px;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: #A4A4A3;
    cursor: default;
}

.calc .ui-slider-horizontal {
    background: #D9D9D9;
    height: 2px;
    border: 0;
    cursor: pointer;
}

.calc .ui-slider-range {
    background: $blue;
    position: relative;
    height: 2px;
}

.calc .ui-slider-handle {
    margin-left: -6px;
    background: #fff;
    position: relative;
    top: -10px;
    display: flex;
    justify-content: center;
    width: 15px;
    height: 15px;
    font-size: 14px;
    border-radius: 50%;
    border: 1px solid $blue;
    cursor: pointer;
}

.calc .ui-slider-handle::before {
    content: attr(data-val);
    position: relative;
    top: -25px;
    display: inline-block;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: $black;
}

.calc .slider-value {
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
}

.calc .slider-value span {
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: #A4A4A3;
}

.calc__checkbox {
    margin-top: 45px;
}

.calc__checkbox .calc-form-row {

    @include breakpoint(lg) {
        align-items: center;
    }
}

.calc__checkbox .calc-form__label {
    margin-bottom: 14px;

    @include breakpoint(lg) {
        margin-bottom: 0;
    }
}

.calc-form__checkbox {
    display: flex;

    @include breakpoint(lg) {
        margin-left: 90px;
    }
}

.checkbox-select label {
    padding: 13px 30px;
    background: #FFFFFF;
    display: block;
    font-size: 14px;
    border: 1px solid #EAEAEA;
    cursor: pointer;
}

.checkbox-select input:checked + label {
    background: $blue;
    color: #fff;
    border: 1px solid $blue;
}

.calc-form__right {
    margin-top: 35px;

    @include breakpoint(lg) {
        margin-top: 0;
        width: calc(((100% / 12) * 3) - 40px);
    }
}

.calc-form__image {
    padding: 0 70px;

    @include breakpoint(md) {
        padding: 0 200px;
    }

    @include breakpoint(lg) {
        padding: 0;
    }
}

.calc-form__bottom {
    margin-top: 56px;
    padding: 30px 0 20px;
    border-top: 1px solid #EAEAEA;

    @include breakpoint(lg) {
        padding: 45px 0 0;
    }
}

.calc-form__bottom .button-style {
    margin: 0 auto;
    display: block;
    max-width: 220px;
    text-align: center;

    @include breakpoint(lg) {
        margin: 0 0 0 auto;
    }
}