.header {
    padding: 12px 0 14px;
    background: #fff;

    @include breakpoint(lg) {
        padding: 18px 0 0;
    }
}

.header__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__logo {
    min-width: 77px;

    @include breakpoint(lg) {
        padding-bottom: 16px;
        min-width: 95px;
    }

    @include breakpoint(xl) {
        min-width: 134px;
    }
}

.header__burger {
    margin-right: 14px;
    font-size: 16px;
    line-height: 1;

    @include breakpoint(lg) {
        display: none;
    }
}

.header__burger.opened {
    background: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 41px;
    height: 41px;
    border-radius: 3px;
    z-index: 9;
}

.header__burger.opened .fa-bars {
    display: none;
}

.header__burger .fa-times {
    display: none;
    font-weight: 300;
    font-size: 20px;
    line-height: 20px;
}

.header__burger.opened .fa-times {
    display: block;
}

.nav {

    @include breakpointMax(lg) {
        padding: 50px 10px;
        background: #fff;
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        max-width: 304px;
        z-index: 9;
        overflow-y: scroll;
        transition: all 0.4s ease;
    }

    @include breakpoint(lg) {
        display: flex;
    }
}

.nav.opened {
    left: 0;
}

.nav__logo {
    width: 77px;

    @include breakpoint(lg) {
        display: none;
    }
}

.nav__menu {
    margin-top: 24px;

    @include breakpoint(lg) {
        margin-top: 0;
        display: flex;
    }
}

.nav__menu-item {

    &.active {
        .nav__menu-link {
            color: $blue;
        }
    }

}


.nav__menu-item:hover .nav__menu-link {

    @include breakpoint(lg) {
        color: $blue;
    }
}

.nav__menu-item ~ .nav__menu-item {
    border-top: 1px solid #E7E7E7;

    @include breakpoint(lg) {
        margin-left: 25px;
        border: none;
    }

    @include breakpoint(xl) {
        margin-left: 32px;
    }
}

.nav__menu-item.has-child {

    @include breakpoint(lg) {
        padding-bottom: 16px;
        position: relative;
        display: flex;
        align-items: center;
    }
}

.nav__menu-item.has-child:first-child::before {

    @include breakpoint(lg) {
        content: '';
        margin-right: 8px;
        background: url(data:image/svg+xml;base64,);
        display: inline-block;
        width: 20px;
        height: 11px;
    }
}

.nav__menu-item.has-child:first-child:hover::before {

    @include breakpoint(lg) {
        background: url(data:image/svg+xml;base64,);
    }
}

.nav__menu-link {
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;

    @include breakpoint(lg) {
        font-size: 13px;
        line-height: 16px;
        white-space: nowrap;
    }
}

.nav__menu-item.has-child .nav__menu-link::after {

    @include breakpointMax(lg) {
        content: '\f054';
        font-family: $icon;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
        color: #AF936C;
        transition: all 0.4s ease;
    }
}

.nav__menu-item.has-child.opened .nav__menu-link::after {

    @include breakpointMax(lg) {
        transform: rotate(90deg);
    }
}

.nav__submenu {
    padding-left: 10px;
    padding-bottom: 20px;

}

.sub-menu {
    display: none;

    @include breakpoint(lg) {
        padding: 0!important;
        background: #FFFFFF;
        position: absolute;
        top: 100%;
        left: 0;
        width: 288px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        z-index: 2;
    }
}

.has-child:hover > .sub-menu {

    @include breakpoint(lg) {
        display: block;
    }
}

.sub-menu .sub-menu {

    @include breakpoint(lg) {
        top: 0;
        left: 100%;
        width: 240px;
        box-shadow: 3px 4px 4px rgba(0, 0, 0, 0.25);
    }
}

.nav__submenu-item {
    text-transform: uppercase;
}

.nav__submenu-item ~ .nav__submenu-item  {

    @include breakpoint(lg) {
        border-top: 1px solid #E7E7E7;
    }
}

.nav__submenu-item.has-child .nav__submenu-link::after {
    content: '\f054';
    font-family: $icon;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: #AF936C;
}

.nav__submenu-item.has-child.opened .nav__submenu-link::after {
    transform: rotate(90deg);
}

.nav__submenu-link {
    padding: 5px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    line-height: 20px;

    @include breakpoint(lg) {
        padding: 15px 13px;
        font-weight: 500;
        font-size: 13px;
        line-height: 16px;
        color: $black;
    }
}

.nav__submenu-link:hover {

    @include breakpoint(lg) {
        background: $gray;
    }
}

.nav__submenu-second {
    padding-top: 10px;
    padding-left: 10px;
    padding-bottom: 20px;
}

.nav__submenu-item-second ~ .nav__submenu-item-second  {

    @include breakpoint(lg) {
        border-top: 1px solid #E7E7E7;
    }
}

.nav__submenu-link-second {
    padding: 5px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include breakpoint(lg) {
        padding: 15px 13px;
        font-weight: 500;
        font-size: 13px;
        line-height: 16px;
        color: $black;
    }
}

.nav__submenu-link-second:hover {

    @include breakpoint(lg) {
        background: $gray;
    }
}

.nav__submenu-item-second.has-child .nav__submenu-link-second::after {
    content: '\f054';
    font-family: $icon;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: #AF936C;
}

.nav__submenu-item-second.has-child.opened .nav__submenu-link-second::after {
    transform: rotate(90deg);
}

.nav__submenu-third {
    padding-top: 10px;
    padding-left: 10px;
    padding-bottom: 20px;
}

.nav__submenu-item-third ~ .nav__submenu-item-third  {

    @include breakpoint(lg) {
        border-top: 1px solid #E7E7E7;
    }
}

.nav__submenu-link-third {
    padding: 5px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include breakpoint(lg) {
        padding: 15px 13px;
        font-weight: 500;
        font-size: 13px;
        line-height: 16px;
        color: $black;
    }
}

.nav__submenu-link-third:hover {

    @include breakpoint(lg) {
        background: $gray;
    }
}

.has-child {

    @include breakpoint(lg) {
        position: relative;
    }
}

.hidden-navbar {
    display: none;

    @include breakpoint(lg) {
        margin-left: 14px;
        padding: 18px  16px;
        display: block;
        font-weight: 700;
        font-size: 13px;
        line-height: 1;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        cursor: pointer;
    }

    @include breakpoint(lg) {
        margin-left: 20px;
    }

    @include breakpoint(xl) {
        margin-left: 40px;
    }
}

.hidden-navbar.hidden {

    @include breakpoint(md) {
        display: none;
    }
}

.hidden-navbar span {

    @include breakpoint(md) {
        display: flex;
    }
}

.hidden-navbar span:hover {

    @include breakpoint(lg) {
        color: $blue;
    }
}

.hidden-navbar:hover .hidden-list {

    @include breakpoint(md) {
        display: block;
    }
}

.hidden-menu.hidden {
    display: none;
}

.hidden-navbar {
    position: relative;
}

.hidden-list {
    display: none;
    position: absolute;
}

.hidden-list.sub-menu {

    @include breakpoint(lg) {
        width: 170px;
    }
}

.hidden-list .nav__menu-item {

    @include breakpoint(lg) {

    }
}

.hidden-list .nav__menu-item ~ .nav__menu-item {

    @include breakpoint(lg) {
        margin: 0;
        border-top: 1px solid #E7E7E7;
    }
}

.hidden-list .nav__menu-item:hover .nav__menu-link {

    @include breakpoint(lg) {
        color: unset;
    }
}

.hidden-list .nav__menu-link {

    @include breakpoint(lg) {
        padding: 15px 13px;
    }
}

.hidden-list .nav__menu-link:hover {

    @include breakpoint(lg) {
        background: $gray;
    }
}

.nav__phone {
    margin-top: 105px;
    padding-left: 28px;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;

    @include breakpoint(lg) {
        display: none;
    }
}

.nav__callback {
    margin-top: 24px;
    padding-left: 28px;

    @include breakpoint(lg) {
        display: none;
    }
}

.nav__callback button {
    position: relative;
    padding: 13px 24.5px;
    width: 228px;
    text-align: right;
}

.nav__callback span::before {
    content: '\f178';
    position: absolute;
    left: 24.5px;
    font-family: $icon;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #FFFFFF;
}

.nav-desc__phone {
    display: none;

    @include breakpoint(lg) {
        padding-bottom: 16px;
        display: block;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        white-space: nowrap;
    }
}