html {
    scroll-behavior: smooth;
  }

body {
    -webkit-text-size-adjust: none;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #F6F6F6;
}

body.overflow-hidden {
    overflow: hidden;
}

body b {
    font-weight: 700;
    color: #000;
}

::-webkit-scrollbar {
    width: 0;
    height: 0;

    @include breakpoint(lg) {
        width: 2px;
        height: 10px;
    }
}

// ::-webkit-scrollbar-thumb {
//     background: $blue;
// }

::-webkit-scrollbar-track {
    background: #f7f7f7;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance:none;
}

main {
    min-height: calc(max(calc(100vh - 614px), 650px));

    @include breakpoint(md) {
        min-height: calc(max(calc(100vh - 614px), 570px));
    }
}

.hide {
    display: none!important;
}

#page {
    position: relative;
    min-width: 375px;
    min-height: 100%;
    max-width: 100%;
    overflow: hidden;
}

#page::before {
    content: '';
}

#page.bg-overlay::before {
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    z-index: 2;
    transition: all 0.5s ease;
}

.container {
    margin: 0 auto;
    padding: 0 10px;
    position: relative;
    max-width: 1260px;
    box-sizing: border-box;

    @include breakpoint(lg) {
        padding: 0 30px;
    }

    // @include breakpoint(xl) {
    //     padding: 0 100px;
    // }
}

.container--wide {

    @include breakpoint(lg) {
        max-width: 1660px;
    }
}

input {
    background: transparent;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    box-sizing: content-box;
}

textarea {
    background: transparent;
    position: relative;
    width: 100%;
    height: 100%;
    resize: none;
    border: none;
    outline: none;
    box-sizing: content-box;
}

.main-title {
    font-weight: 700;
    font-size: 27px;
    line-height: 140%;
    color: #000;
    text-transform: uppercase;

    @include breakpoint(md) {
        font-size: 45px;
    }

    @include breakpoint(hd) {
        font-size: 60px;
    }
}

.button-style {
    padding: 13px 30px;
    display: inline-block;
    font-weight: 400;
    font-size: 14px;
    line-height: 1;
    text-transform: uppercase;
    color: #FFFFFF;
    cursor: pointer;
}

.button-style--blue {
    background: $blue;
}

.button-style--gray {
    background: $gray;
    color: $black;
}

.button-style--transparent {
    border: 1px solid #FFFFFF;
}

.button-style.arrow-back {
    display: flex;
    align-items: center;
    max-width: 220px;
}

.button-style.arrow-back span {
    display: flex;
    align-items: center;
}

.button-style.arrow-back::before {
    content: '';
    margin-right: 10px;
    background: url('data:image/svg+xml;base64,') no-repeat;
    width: 13px;
    height: 14px;
}

.input-style  {
    padding-bottom: 12px;
    border-bottom: 1px solid #E0E0E0;
}

.input-style input {
    height: 30px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #222;
    box-sizing: border-box;
}

.input-style input::placeholder {
    font-size: 16px;
    color: #999999;
}

.checkbox-style input {
    display: none;
}

.checkbox-style label {
    position: relative;
    display: flex;
    font-size: 14px;
    line-height: 140%;
    transform: translateY(5px);
    cursor: pointer;
    user-select: none;
}

.checkbox-style label::before {
    content: '';
    margin-right: 10px;
    display: inline-block;
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    border: 2px solid #EAEAEA;
    transform: translateY(-3px);
}

.checkbox-style input:checked + label::after {
    content: '';
    background: $blue;
    position: absolute;
    top: 4px;
    left: 7px;
    display: inline-block;
    width: 10px;
    height: 10px;
}

.overflow {
    overflow: hidden;
}

.textarea-style {
    border: 1.5px solid #E0E0E0;
}

.textarea-style textarea {
    padding: 11px 15px;
}

.textarea-style textarea::placeholder {
    max-width: 175px;
    font-size: 12px;
    line-height: 150%;
    color: #828282;
}

section {
    padding: 30px 0 60px;

    @include breakpoint(md) {
        padding: 40px 0 80px;
    }

    @include breakpoint(lg) {
        padding: 40px 0 100px;
    }

    @include breakpoint(xl) {
        padding: 50px 0 120px;
    }

    @include breakpoint(hd) {
        padding: 60px 0 150px;
    }
}
