.top-head {
    padding: 0 10px;
    background: #fff;

    @include breakpoint(xl) {
        padding: 0;
    }
}

.head-title {
    margin: 0 auto;
    padding: 34px 0;
    background: linear-gradient(0deg, rgba(40, 44, 54, 0.67), rgba(40, 44, 54, 0.67)), url('../images/bc-bg-mob.jpg') no-repeat;
    background-size: cover;
    max-width: 1600px;
    height: 140px;

    @include breakpoint(md) {
        background: linear-gradient(0deg, rgba(40, 44, 54, 0.67), rgba(40, 44, 54, 0.67)), url('../images/bc-bg.jpg') no-repeat;
        background-size: cover;
    }

    @include breakpoint(xl) {
        height: 180px;
    }
}

.head-title .container {
    height: 100%;
}

.page-title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

.page-title h1 {
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    color: #fff;
    text-align: center;

    @include breakpoint(md) {
        margin-left: 25px;
        text-align: left;
    }

    @include breakpoint(lg) {
        font-size: 40px;
        line-height: 49px;
    }

    @include breakpoint(xl) {
        margin-left: 0;
    }
}

.breadcrumbs {
    padding: 25px 0;

    @include breakpoint(lg) {
        padding: 35px 0 32px;
    }
}

.breadcrumbs .container {

    @include breakpointMax(lg) {
        padding: 0;
    }
}

.breadcrumbs-wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.breadcrumbs-item {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #2B2A29;
}

.breadcrumbs-item span {
    @include textLimit(1);
}

.breadcrumbs-item a {
    color: #00A0E3;
}

.breadcrumbs-separator {
    margin: 0 5px;
}