.page__faq {
    padding: 13px 0 60px 0;

    @include breakpoint(md) {
        padding: 40px 0 60px 0;
    }

    @include breakpoint(lg) {
        padding: 40px 0 80px 0;
    }

}

.page__faq-title {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #2B2A29;

    @include breakpoint(md) {
        font-size: 36px;
    }

}

.page__faq-text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #000000;
    margin-top: 30px;
    margin-bottom: 15px;

    @include breakpoint(md) {
        margin-bottom: 37px;
    }

}

.faq__title {
    position: relative;
    display: block;
    -webkit-transition: background-color 0.2s;
    transition: background-color 0.2s;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #222222;
    padding: 16px 30px 16px 9px;
    background: #FFFFFF;

    @include breakpoint(lg) {
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        padding: 23px 30px 23px 30px;
    }

}

.b-faq {
    margin-bottom: 10px;
}


.faq__title:hover {}

.faq__active {}

.faq__title .faq__spoiler {
    position: absolute;
    bottom: 15px;
    right: 10px;
    display: inline-block;
    vertical-align: middle;
    border-radius: 50%;
    -webkit-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;

    @include breakpoint(lg) {
        bottom: 20px;
        right: 20px;
    }


}

.faq__rotate {
    transform: rotate(180deg);
}

.faq__content {
    padding: 0px 12px 15px 12px;
    font-size: 14px;
    display: none;
    background-color: #FFFFFF;

    p,
    li,
    ul {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #535352;
        margin-bottom: 10px;
    }


    @include breakpoint(md) {
        padding: 0 30px 23px 30px;
    }

}

.faq__symbol {
    font-weight: 700;
}

.faq__content-info {
    display: flex;
    flex-wrap: wrap;

    img {
        margin-bottom: 15px;

        @include breakpoint(lg) {
            max-width: 260px;
            margin-right: 40px;
        }

    }

    @include breakpoint(lg) {
        flex-direction: row;
        flex-wrap: unset;
    }


}
