.page__sertificates {

    padding: 13px 0 68px 0;


    @include breakpoint(md) {
        padding: 30px 0 60px 0;
    }


    @include breakpoint(lg) {
        padding: 40px 0 90px 0;
    }

}

.container {}

.sertificates__title {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #2B2A29;
    margin-bottom: 40px;

    
    @include breakpoint(lg) {
        font-size: 36px;
        margin-bottom: 60px;
    }  

}

.seftificates__items {
    overflow: hidden;
}

.swiper-wrapper {}

.swiper-slide {}

.seftificates__item {
    display: flex;
    flex-direction: column;
}

.seftificates__item-img {
    height: 392px;
    border: 1px solid #00A0E3;
    position: relative;
    background: #fff;
}

.seftificates__item-img-icon {
    position: absolute;
    width: 20px;
    height: 28px;
    bottom: 15px;
    right: 15px;
}

.seftificates__item-type {
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 140%;
    text-transform: uppercase;
    color: #00A0E3;
    margin-top: 28px;
    margin-bottom: 17px;

    @include breakpoint(md) {
        font-size: 14px;
    }

}

.seftificates__item-title {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #2B2A29;
}

.sertificates__btns {
    margin-top: 54px;
}