.footer {
    background: #535352;
    padding-top: 20px;
    padding-bottom: 40px;

    @include breakpoint(lg) {
        padding-top: 33px;
        padding-bottom: 30px;
    }

}
.footer__top {
    border-bottom: 1px solid #A4A4A3;
}
.footer__top-nav {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    padding-bottom: 15px;

    @include breakpoint(lg) {
        justify-content: space-between;
    }


}
.footer__top-nav-item {
    display: flex;
    align-items: center;
    width: calc( 100% / 2 - 10px);
    margin-left: 10px;
    margin-bottom: 10px;
    background: #7C7B7B;
    border-radius: 5px;
    border: 1px solid #7C7B7B;
    padding: 11px;

    a {
        display: flex;
        flex-direction: row;
        align-items: center;

        img {
            min-width: 22px;
            min-height: 32px;
            max-width: 22px;
            max-height: 32px;
            margin-right: 10px;
    
            @include breakpoint(xl) {
                min-width: 32px;
                min-height: 32px;
                max-width: 32px;
                max-height: 32px;
            }
        }
    
        span {
            font-style: normal;
            font-weight: 400;
            font-size: 13px;
            line-height: 16px;
            color: #FFFFFF;
    
        }
    

    }

    &.active {
        border: 1px solid #00A0E3;
    }


    @include breakpoint(lg) {
        width: calc( 100% / 6 - 10px);
        padding-right: 25px;
    }

    @include breakpoint(xl) {
        width: calc( 100% / 6 - 30px);
    }


}
.footer__middle {
    margin-top: 25px;

    @include breakpoint(lg) {
        margin-top: 50px;
    }

}

.footer__middle-wrapper {
    border-bottom: 1px solid #A4A4A3;
    padding-bottom: 25px;


    @include breakpoint(lg) {
        display: flex;
        justify-content: space-between;
    }

}

.footer__logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    @include breakpoint(lg) {
        flex-direction: column;
    }

}
.footer__logo-img {
    min-width: 134px;
    min-height: 66px;
    display: flex;
    margin-right: auto;
    img {
        @include breakpoint(lg) {

        }
    }

}
.footer__logo-title {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: #FFFFFF;
    margin-left: 55px;
    max-width: 237px;
    margin-right: auto;

    @include breakpoint(lg) {
        margin-left: 0;
        font-size: 13px;
        min-width: 254px;
        margin-bottom: auto;
        margin-top: 40px;
    }

}

.footer__middle-nav {
    margin-top: 30px;

    @include breakpoint(lg) {
        margin-top: 0;
        max-width: 360px;
    }

}

.footer__middle-nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.footer__middle-nav-item {
    width: calc( 100% / 2 - 10px);
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    margin-bottom: 18px;
    text-transform: uppercase;
    color: #FFFFFF;
}

.footer__middle-info {
}

.footer__middle-phone {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    margin-top: 19px;
}

.footer__middle-btn {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 140%;
    text-transform: uppercase;
    color: #FFFFFF;
    border: 1px solid #EAEAEA;
    padding: 10px 15px;
    width: fit-content;
    margin-top: 20px;
    margin-bottom: 30px;
    cursor: pointer;
}

.footer__middle-text {
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 150%;
    color: #CDCCCC;
    margin-bottom: 20px;
    max-width: 239px;

    &:last-child {
        margin-bottom: 0;
    }
}

.footer__bottom {
    padding-top: 30px;
}

.footer__bottom-wrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;


    @include breakpoint(lg) {
        flex-direction: row;
        justify-content: space-between;
    }

}

.footer__bottom-link {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #CDCCCC;
    width: 100%;
    margin-bottom: 10px;

    @include breakpoint(lg) {
        width: fit-content;
    }

}