

.page__products-section {
    .seo__section {
        margin-bottom: 40px;

        @include breakpoint(lg) {
            margin-bottom: 60px;
        }


        @include breakpoint(lg) {
            margin-bottom: 140px;
        }

    }
}


.page__products-title {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  color: #2b2a29;
  padding: 20px 0;

    @include breakpoint(md) {
        font-size: 30px;
        margin-top: 40px;
        margin-bottom: 40px;
    }


    @include breakpoint(lg) {
       margin-top: 50px;
       margin-bottom: 80px;
       font-size: 36px;
    }

}

