.hero {
    background: #fff;

    @include breakpoint(lg) {
        height: calc(100vh - 82px);
        min-height: 560px;
        max-height: 560px;
    }

    @include breakpoint(xl) {
        height: calc(100vh - 101px);
        max-height: 780px;
    }
}

.hero .container {

    @include breakpoint(xl) {
        height: 100%;
    }
}

.hero-slider {

    @include breakpoint(xl) {
        height: calc(100% - 52px);
    }
}

.hero-slider .swiper-slide {
    height: auto;
}

.hero__slide {
    padding: 26px 24px 48px;
    background-size: cover;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;

    @include breakpoint(lg) {
        padding: 100px 80px 80px;
        justify-content: flex-end;
    }

    @include breakpoint(xl) {
        margin: 0 auto;
        padding: 200px 0 130px;
        max-width: 1200px;
    }
}

.hero__slide::before {
    content: '';
    background: rgb(40, 44, 54);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.1;
    z-index: 0;
}

.hero__slide-title {
    margin: auto auto 0;
    position: relative;
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    text-align: center;
    color: #fff;
    z-index: 1;

    @include breakpoint(lg) {
        margin: 0;
        font-size: 40px;
        line-height: 49px;
        text-align: left;
    }

    @include breakpoint(xl) {
        font-size: 48px;
        line-height: 59px;
    }
}

.hero__slide-text {
    margin: 20px auto 0;
    position: relative;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    text-align: center;
    color: #fff;
    z-index: 1;

    @include breakpoint(lg) {
        margin: 45px 0 0;
        font-size: 20px;
        line-height: 140%;
        text-align: left;
    }

    @include breakpoint(xl) {
        margin: 60px 0 auto;
        font-size: 24px;
    }
}

.hero__actions {
    margin: auto auto 0;
    position: relative;
    max-width: 270px;
    text-align: center;
    z-index: 1;

    @include breakpoint(lg) {
        margin: 40px 0 0;
        display: flex;
        max-width: unset;
    }
}

.hero__actions .button-style--transparent {

    @include breakpointMax(lg) {
        margin-top: 26px;
    }
}

.hero__actions .button-style {

    @include breakpointMax(lg) {
        width: 100%;
    }
}

.hero__actions .button-style--blue {
    display: flex;
    align-items: center;
    justify-content: center;

    @include breakpointMax(lg) {
        margin-top: 14px;
    }

    @include breakpoint(lg) {
        margin-left: 30px;
    }
}

.hero__actions .button-style--blue::after {
    content: '\f178';
    margin-left: 10px;
    font-family: $icon;
}

.hero-slider-pagination {
    position: absolute;
    text-align: center;
    bottom: 15px!important;
    z-index: 1;
}

.hero-slider-pagination .swiper-pagination-bullet {
    margin: 0 8px!important;
    background: #FFFFFF;
    position: relative;
    width: 6px;
    height: 6px;
    opacity: 0.8;
}

.hero-slider-pagination .swiper-pagination-bullet-active {
    background: rgba(255,255,255,0.3);
}

.hero-slider-pagination .swiper-pagination-bullet-active::before {
    content: '';
    position: absolute;
    top: -3px;
    left: -3px;
    width: 12px;
    height: 12px;
    border: 1px solid #FFFFFF;
    border-radius: 50%;
}

.hero__bottom {
    margin-top: 16px;
    padding-bottom: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include breakpoint(lg) {
        position: relative;
    }
}

.hero-slider-buttons {
    display: flex;
    z-index: 1;
}

.hero-slider-buttons .button {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: $black;
    cursor: pointer;
}

.hero-slider-buttons .button-next {
    margin-left: 25px;
}

.hero-slider-buttons .swiper-button-disabled {
    color: #CDCCCC;
}

.hero-slider-desc {
    position: absolute;
    width: 100%;
    text-align: center;

    @include breakpointMax(lg) {
        display: none;
    }
}

.hero-slider-desc p {

    @include breakpoint(lg) {
        margin: 0 auto;
        max-width: 700px;
        font-size: 14px;
        line-height: 17px;
        color: #7C7B7B;
    }
}

.hero-callback {
    font-weight: 500;
    font-size: 12px;
    line-height: 140%;
    text-transform: uppercase;
    color: #265D75;
}

