.page__projects {
  padding: 13px 0 68px 0;

  @include breakpoint(md) {
    padding: 30px 0 60px 0;
  }

  @include breakpoint(lg) {
    padding: 40px 0 90px 0;
  }
}

.page__projects-title {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  color: #2b2a29;
  margin-bottom: 20px;

  @include breakpoint(md) {
    font-size: 36px;
    margin-bottom: 45px;
  }

}

.page__projects-items {
  display: flex;
  flex-wrap: wrap;

  @include breakpoint(md) {
    margin-left: -20px;
  }

  @include breakpoint(lg) {
    margin-left: -30px;
  }
}

.page__projects-item {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;

  @include breakpoint(md) {
    width: calc(100% / 2 - 20px);
    margin-left: 20px;
  }

  @include breakpoint(lg) {
    width: calc(100% / 3 - 30px);
    margin-left: 30px;
  }
}

.page__projects-item-img {
  background: #fff;
  max-height: 270px;
  min-height: 270px;

  @include breakpoint(md) {
    max-height: 290px;
    min-height: 290px;
  }

}
.page__projects-item-type {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  text-transform: uppercase;
  color: #00a0e3;
  margin: 18px 0;
}

.page__projects-item-title {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #2b2a29;
  margin-bottom: 18px;
}

.page__projects-item-text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #2b2a29;
}

.page__rewievs {
    background: #fff;

    padding: 25px 0 68px 0;

    @include breakpoint(md) {
      padding: 35px 0 60px 0;
    }
  
    @include breakpoint(lg) {
      padding: 40px 0 90px 0;
    }
}