.page__about {
  padding: 35px 0;

  p {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #000000;
    margin-bottom: 15px;
  }

  ul {
    padding-left: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  li {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #2b2a29;
    margin-bottom: 5px;
    list-style: disc;
  }

  h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    color: #2b2a29;
    margin-top: 25px;
    margin-bottom: 25px;

    @include breakpoint(lg) {
        font-size: 24px;
    }
  }

  @include breakpoint(lg) {
    padding-bottom: 93px;
  }
}

.page__about-items {
  display: flex;
  flex-wrap: wrap;
  padding: 20px 10px;
  background: #ffff;
  grid-gap: 14px;
  column-gap: 14px;
  margin-top: 60px;


  @include breakpoint(lg) {
    justify-content: center;
    padding: 43px 86px;
  }

}

.page__about-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 48%;

    @include breakpoint(lg) {
        width: 23%;
    }

}

.page__about-item-icon {
  width: 60px;
  height: 60px;
}

.page__about-item-title {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: #7c7b7b;
  margin-left: 8px;

    @include breakpoint(lg) {
        max-width: 113px;
    }


}
